import React from 'react'
import { Paper, Typography, Button, TextField, Grid, Box, Modal, IconButton, Accordion, AccordionSummary, AccordionDetails, Breadcrumbs, Link } from '@mui/material'
import { ShoppingCart, Close, Payment, CheckCircle, LocalShipping, Star, VerifiedUser, ExpandMore, FavoriteBorder, Home, Storefront, NavigateNext, Label, Category } from '@mui/icons-material'
import useBuys from './useBuys'
import ProductCarrusel from '../../Components/Carrusel/ProductCarrusel/productCarrusel'
import CardCarrusel from '../../Components/Carrusel/CardCarrusel/CardCarrusel'

const SingleProductPurchase = () => {
  const { state, handlers, context } = useBuys()

  return (
    <Box display="flex" justifyContent="center" px={2} py={4}>
      <Paper sx={{ boxShadow: { xs: 'none', md: '0px 4px 10px rgba(0, 0, 0, 0.2)' }, width: { xs: '100%', sm: '100%', md: '100%', lg: '80%', xl: '80%', }, p: 2 }}>
        <Typography variant="h4" gutterBottom textAlign="center" sx={{ fontWeight: 'bold', color: '#00004e' }}>{context.contextProduct.name}</Typography>

        <Breadcrumbs aria-label="breadcrumb" separator={<NavigateNext fontSize="small" sx={{ color: '#00004e' }} />}
          sx={{ mb: 2, padding: '8px 16px', borderRadius: 2, color: '#00004e', fontFamily: 'Orbitron, sans-serif' }}>
          <Link href="/home/products" underline="hover" sx={{ display: 'flex', alignItems: 'center', color: '#00004e' }}>
            <Storefront sx={{ mr: 0.5, color: '#00004e' }} fontSize="small" /> Productos
          </Link>
          <Link href={`/home/products/brand/${context.contextProduct.brand}`} underline="hover" sx={{ display: 'flex', alignItems: 'center', color: '#00004e' }}>
            <Label sx={{ mr: 0.5, color: '#00004e' }} fontSize="small" /> {context.contextProduct.brand}
          </Link>
          <Link href={`/home/products/category/${context.contextProduct.category}`} underline="hover" sx={{ display: 'flex', alignItems: 'center', color: '#00004e' }}>
            <Category sx={{ mr: 0.5, color: '#00004e' }} fontSize="small" /> {context.contextProduct.category}
          </Link>
        </Breadcrumbs>

        <Grid container spacing={4}>
          <Grid item xs={12} md={6}>
            <Box sx={{
                width: '100%',
                height: '400px',
                position: 'relative',
                overflow: 'hidden',
                borderRadius: 1,
                cursor: 'zoom-in'
              }}
              onClick={() => state.setZoomed(true)}>
              <Box width="100%" height="100%" display="flex" justifyContent="center">
                <Box component="img" src={state.selectedImage || context.contextProduct.image} alt={context.contextProduct.name} />
              </Box>
            </Box>

            <ProductCarrusel
              product={context.contextProduct}
              selectedImage={state.selectedImage}
              onSelectImage={state.setSelectedImage}
              customBreakpoints={{
                320:   { slidesPerView: 3 },
                375:   { slidesPerView: 4 },
                600:   { slidesPerView: 5 },
                750:   { slidesPerView: 7 },
                900:   { slidesPerView: 4 },
                1120:   { slidesPerView: 6 },
                1200:   { slidesPerView: 5 },
                1750:  { slidesPerView: 7 }
              }}
            />

            <Modal open={state.zoomed} onClose={() => state.setZoomed(false)}>
              <Box
                sx={{
                  position: 'fixed',
                  top: 0,
                  left: 0,
                  width: '100vw',
                  height: '100vh',
                  bgcolor: 'rgba(0,0,0,0.9)',
                  display: 'flex',
                  justifyContent: 'center',
                  alignItems: 'center',
                }}>
                <IconButton onClick={() => state.setZoomed(false)}
                  sx={{
                    position: 'absolute',
                    top: 16,
                    right: 16,
                    color: '#fff',
                    zIndex: 10,
                  }}>
                  <Close />
                </IconButton>
                <Box component="img"
                  src={state.selectedImage || context.contextProduct.image}
                  alt={context.contextProduct.name}
                  sx={{
                    width: 'auto',
                    height: 'auto',
                    maxWidth: '95vw',
                    maxHeight: '95vh',
                    objectFit: 'contain',
                    borderRadius: '8px',
                    boxShadow: 4,
                  }}
                />
              </Box>
            </Modal>
          </Grid>

          <Grid item xs={12} md={6}>
            <Typography variant="body1" gutterBottom>{context.contextProduct.description}</Typography>
            
            <Box display="flex" alignItems="center" sx={{ justifyContent: { xs: 'space-between', md: 'start' } }} gap={4}>
              <Typography variant="h6" fontSize={30}>${context.contextProduct.price.toLocaleString()}</Typography>
              <Box display="flex" alignItems="center">
                <Typography variant="subtitle1">Cantidad:</Typography>
                <Box display="flex" alignItems="center" sx={{ py: 2 }}>
                  <TextField
                    value={1}
                    variant="outlined"
                    size="small"
                    inputProps={{ style: { textAlign: 'center', width: 40, height: 30 } }}
                    sx={{ mx: 1 }}
                    readOnly
                  />
                  <Box display="flex" flexDirection="column" alignItems="center">
                    <Button variant="outlined" color="primary" size="small" sx={{ minWidth: 30, padding: 0 }}>+</Button>
                    <Button variant="outlined" color="primary" size="small" disabled={2 === 1} sx={{ minWidth: 30, padding: 0, mt: 0.5 }}>-</Button>
                  </Box>
                </Box>
              </Box>
            </Box>

            <Grid container spacing={2}>
                <Grid item xs={6} lg={6}>
                  <Box
                    display="flex"
                    alignItems="center"
                    gap={1}
                    sx={{
                      mt: 2,
                      color: "success.main", // color verde
                      fontWeight: "bold",
                    }}
                  >
                    <CheckCircle />
                    <Typography variant="body1">Stock disponible</Typography>
                  </Box>

                  <Box
                    display="flex"
                    alignItems="center"
                    gap={1}
                    sx={{
                      mt: 1,
                      color: "info.main",
                      fontWeight: "bold",
                    }}>
                    <LocalShipping />
                    <Typography variant="body1">Entrega inmediata</Typography>
                  </Box> 
                  <Box
                    display="flex"
                    alignItems="center"
                    gap={0.5}
                    sx={{ mt: 1 }}>
                    {[...Array(5)].map((_, index) => (
                      <Star
                        key={index}
                        sx={{ color: index < state.rating ? "#FFD700" : "#E0E0E0" }} // rating = 0-5
                      />
                    ))}
                    <Typography variant="body2" sx={{ ml: 1, fontWeight: 500 }}>
                      {state.rating.toFixed(1)} / 5
                    </Typography>
                  </Box> 
                </Grid>
                <Grid item xs={6} lg={6} display="flex" justifyContent="end" alignItems="center">
                  <Box
                    display="flex"
                    flexDirection="column"
                    sx={{
                      mt: 1,
                      color: "primary.main",
                    }}>
                    <Box display="flex" alignItems="center" gap={1}>
                      <VerifiedUser />
                      <Typography variant="body1" fontWeight="bold">
                        Garantía asegurada:
                      </Typography>
                    </Box>

                    <Box sx={{ pl: 4, mt: 0.5 }}>
                      <Typography variant="body2" sx={{ fontSize: { xs: 10, sm: 15 } }}>✔ Garantía oficial de la marca</Typography>
                      <Typography variant="body2" sx={{ fontSize: { xs: 10, sm: 15 } }}>✔ Garantía de nuestra tienda</Typography>
                    </Box>
                  </Box> 
                </Grid>
            </Grid>

            <Box my={2}>
              <Accordion sx={{ boxShadow: 'none', border: '1px solid #e0e0e0', '&::before': { display: 'none' }}}>
                <AccordionSummary expandIcon={<ExpandMore />}>
                  <Box display="flex" alignItems="center" gap={1}>
                    <Payment />
                    <Typography variant="body1">
                      Métodos de Pago ({state.selectedPayment ? 1 : 0}/1)
                    </Typography>
                  </Box>
                </AccordionSummary>
                <AccordionDetails>
                  <Grid container spacing={1}>
                    {context.paymentMethods.map((method) => (
                      <Grid item xs={3} sm={6} md={6} lg={3} key={method.id}>
                        <Box
                          onClick={() => state.setSelectedPayment(method.id)}
                          sx={{
                            cursor: 'pointer',
                            display: 'flex',
                            flexDirection: 'column',
                            alignItems: 'center',
                            padding: 1,
                            borderRadius: 1,
                            backgroundColor: state.selectedPayment === method.id ? '#e0f7fa' : 'transparent',
                            '&:hover': { backgroundColor: '#e0f2f1' },
                          }}
                        >
                          {method.icon}
                          <Typography variant="caption" align="center">{method.label}</Typography>
                        </Box>
                      </Grid>
                    ))}
                  </Grid>
                </AccordionDetails>
              </Accordion>

              <Accordion sx={{ mt: 2, boxShadow: 'none', border: '1px solid #e0e0e0', '&::before': { display: 'none' }}}>
                <AccordionSummary expandIcon={<ExpandMore />}>
                  <Box display="flex" alignItems="center" gap={1}>
                    <LocalShipping />
                    <Typography variant="body1">
                      Opciones de Entrega ({state.deliveryOption ? 1 : 0}/1)
                    </Typography>
                  </Box>
                </AccordionSummary>
                <AccordionDetails>
                  <Box display="flex" gap={2} flexWrap="wrap">
                    {context.deliveryMethods.map((method) => (
                      <Box
                        key={method.id}
                        onClick={() => state.setDeliveryOption(method.id)}
                        sx={{
                          cursor: 'pointer',
                          display: 'flex',
                          flexDirection: 'column',
                          alignItems: 'center',
                          padding: 1,
                          borderRadius: 1,
                          backgroundColor: state.deliveryOption === method.id ? '#e0f7fa' : 'transparent',
                          '&:hover': { backgroundColor: '#e0f2f1' },
                        }}
                      >
                        {method.icon}
                        <Typography variant="caption" align="center">{method.label}</Typography>
                      </Box>
                    ))}
                  </Box>
                </AccordionDetails>
              </Accordion>
            </Box>

            <Box
              my={4}
              sx={{
                display: { xs: 'flex', md: 'flex' },
                flexDirection: { xs: 'column', md: 'row' },
                alignItems: 'center',
                justifyContent: 'center',
                gap: 2,
              }}
            >
              <Button
                variant="contained"
                color="primary"
                startIcon={<Payment />}
                onClick={handlers.handlePurchase}
                fullWidth={false}
              >
                Finalizar Compra
              </Button>

              <Button
                variant="outlined"
                color="secondary"
                startIcon={<FavoriteBorder />}
                // onClick={handlers.handleAddToFavorites}
                fullWidth={false}
              >
                Añadir a Favoritos
              </Button>

              <Button
                variant="outlined"
                color="primary"
                startIcon={<ShoppingCart />}
                onClick={handlers.handleAddToCart}
                fullWidth={false}
              >
                Añadir al Carrito
              </Button>
            </Box>
          </Grid>
        </Grid>

        <Box mb={4}>
          <Typography variant="h6">Ficha Técnica:</Typography>
          <Typography variant="body2">{context.contextProduct.description}</Typography>
        </Box>

        <Box>
          <Typography variant="h4" align="center" gutterBottom sx={{ fontWeight: 'bold', color: '#00004e', mb: 2, mt: 10 }}>Productos similares:</Typography>
          <CardCarrusel 
            cards={context.additionalProduct}
            setIsLoading={null} 
            setSuccess={null} 
            setError={null}
            customBreakpoints={{
              0: { slidesPerView: 1 },
              770: { slidesPerView: 2 },
              1010: { slidesPerView: 3 },
              1200: { slidesPerView: 2 },
              1500: { slidesPerView: 3 },
              1850: { slidesPerView: 4 }
            }}/>
        </Box>
      </Paper>
    </Box>
  )
}

export default SingleProductPurchase